const RuleDraw = () => {
  return (
    <div className="rule">
      <h2>Draw Phase</h2>
      <p>Those are the rules for drawing:</p>
      <ul>
        <li>
          The shapes cannot overlap filled spaced or the edges of the map (mountains and wasteland
          are considered spaces).
        </li>
      </ul>
      <li>Shapes can be rotated and/or mirrored.</li>
      <li>Shapes do not have to connect to a previously drawn shape.</li>
      <li>If each side of a mountain space is filled, fill in the next coin in the coin track.</li>
      <h3>1x1 Square Rule</h3>
      <p>
        If a shape cannot be legally drawn, you must instead draw a 1x1 square anywhere on their map
        and filled with an available terrain type.
      </p>
    </div>
  );
};

const RuleRuins = () => {
  return (
    <div className="rule">
      <h2>Ruins</h2>
      <p>
        When a card contains the Ruin icon, that shape MUST be drawn on top of a ruin.{' '}
        <b>
          If you can not or do not have an available ruin space, you must follow the 1x1 square
          rule.
        </b>
      </p>
    </div>
  );
};

const RuleAmbush = () => {
  return (
    <div className="rule">
      <h2>Ambush</h2>
      <p>When an ambush card is revealed:</p>
      <h3>If all players are in the same location</h3>
      <p>
        Give your map sheet to the player in the indicated direction on the card. They must draw the
        monster shape in your map,following all the drawing rules.{' '}
        <b>If you can't draw the monster, follow the 1x1 square rule.</b> Then return the map to its
        original player.
      </p>
      <h3>If playing solo or online</h3>
      <p>
        Use the icon in the top-right corner as reference. Starting from that corner, without
        rotating or mirroring the shape, see if you can draw the monster shape. If not, move in the
        direction of the arrows square by square until you can draw the shape.
        <b>If you can't draw the monster, follow the 1x1 square rule.</b>
      </p>
    </div>
  );
};

const RuleSkills = () => {
  return (
    <div className="rule">
      <h2>Using a Skill Card</h2>
      <p>TBD</p>
    </div>
  );
};

const RuleSeasonCheck = () => {
  return (
    <div className="rule">
      <h2>Season Threshold Check</h2>
      <p>
        Each season has a time threshold and each explore card has a duration of 0-2 time units. The
        game will keep track of thatthreshold and the season will end when the total of Explore
        cards time units is equal or exceeds the season limit. Then to go scoring.
      </p>
    </div>
  );
};

const RuleScoring = () => {
  return (
    <div className="rule">
      <h2>Scoring a Season</h2>
      <p>Each season will score two different goal cards.</p>
      <p>Calculate how many points you score in each active goal card.</p>
      <p>
        Add 1 point for each unused coin (if playing with Skills coins can be spent and won't
        counthere).
      </p>
      <p>
        Subtract 1 point for each side of monster squares that are not filled with other shapes (or
        the edge of the map or the wasteland).
      </p>
      <p>Go to the next season or end the game if you're done with all 4 seasons.</p>
    </div>
  );
};

const RuleEndGame = () => {
  return (
    <div className="rule">
      <h2>End of the Game</h2>
      <p>Add the points for all 4 seasons. The winner is the player with the most points.</p>
      <p>
        In the case of a tie, the player who lost the least amount of points for monsters, win the
        game.
      </p>
    </div>
  );
};

const ruleComponents = {
  Goal: <RuleGoal />,
  Explore: <RuleExplore />,
  Draw: <RuleDraw />,
  Ruins: <RuleRuins />,
  Ambush: <RuleAmbush />,
  Skills: <RuleSkills />,
  'Season Check': <RuleSeasonCheck />,
  Scoring: <RuleScoring />,
  'End Game': <RuleEndGame />,
};

const Rules = () => {
  // Local States
  const [ruleStep, setRuleStep] = useState(0);

  const handleNext = () => {
    setRuleStep((prevActiveStep) => prevActiveStep + 1);
  };

  const handleBack = () => {
    setRuleStep((prevActiveStep) => prevActiveStep - 1);
  };

  const handleReset = () => {
    setRuleStep(0);
  };

  const ruleComponent = ruleComponents[steps[ruleStep]];

  return (
    <div className="rules">
      <Header title="Rules" />
      <Stepper activeStep={ruleStep} alternativeLabel>
        {steps.map((label) => (
          <Step key={label}>
            <StepLabel>{label}</StepLabel>
          </Step>
        ))}
      </Stepper>
      <div className="rule-container">{ruleComponent}</div>
      <div className="rule-action-buttons">
        <Button
          variant="outlined"
          className="btn btn--outline-filled"
          disabled={ruleStep === 0}
          onClick={handleBack}
        >
          Back
        </Button>
        <Button
          variant="outlined"
          className="btn btn--outline-filled"
          onClick={ruleStep === steps.length - 1 ? handleReset : handleNext}
        >
          {ruleStep === steps.length - 1 ? 'Reset' : 'Next'}
        </Button>
      </div>
    </div>
  );
};

export default Rules;
